<template>
  <a-modal
    :title="title"
    :width="640"
    :visible="visible"
    :confirmLoading="loading"
    @ok="
      () => {
        $emit('ok')
      }
    "
    @cancel="
      () => {
        $emit('cancel')
      }
    "
  >
    <a-spin :spinning="loading">
      <a-form-model ref="bindForm" :model="model" :rules="rules" v-bind="formItemLayout">
        <a-form-model-item label="录像机" prop="recorder_id">
          <a-select show-search placeholder="请选择录像机" v-model="model.recorder_id">
            <a-select-option v-for="(nvr, key) in nvrItems" :value="nvr.id" :key="key">{{
              nvr.device_name ? nvr.device_name : nvr.device_id
            }}</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-spin>
  </a-modal>
</template>

<script>
import { nvrItems } from '@/api/device'

const defaultModel = {
    recorder_id: undefined
}
export default {
    props: {
        visible: {
            type: Boolean,
            default: true
        },
        loading: {
            type: Boolean,
            default: () => false
        }
    },
    data () {
        return {
            title: '绑定录像机',
            model: defaultModel,
            nvrItems: {},
            formItemLayout: {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 }
            },
            rules: {
                recorder_id: [{ required: true, message: '录像机必须选择', trigger: 'blur' }]
            }
        }
    },
    methods: {
        async loadnvrItems () {
          this.nvrItems = await nvrItems()
          console.log('====================================')
          console.log('nvrItems:', this.nvrItems)
          console.log('====================================')
        },
        resetForm () {
            this.model.recorder_id = defaultModel.recorder_id
        }
    },
    watch: {
        async visible (newValue, oldValue) {
            if (newValue) {
              this.loadnvrItems()
            }
        }
    }
}
</script>
<style lang="less" scoped>
.ant-select {
    min-width: 220px;
}
</style>
